<template>
	<view>
		<cu-custom bgColor="bg-gradual-blue" :isBack="true">
			<block slot="content">座位列表</block>
		</cu-custom>
		<!-- <homecom class="homclass"></homecom> -->
		<view class="mainsub">
			<scroll-view scroll-x class="bg-white nav text-center">
				<view class="cu-item" :class="index==TabCur?'text-blue cur':''" v-for="(item,index) in tabArr"
					:key="index" @tap="tabSelect" :data-id="index">
					{{item}}
				</view>
			</scroll-view>

			<view class="cu-bar bg-white solid-bottom">
				<button class="cu-btn bg-green shadow" @click="adduser"><text class="cuIcon-roundadd"
						style="font-size: 1.2em;margin-right: 5px;"></text>新增座位</button>
			</view>
			<view class="text-xsl padding" v-if="listdata.length == 0">
				<text class=" cuIcon-info text-blue"></text>
				<view class="textclass">没有可查看的数据</view>
			</view>
			<view class="card-main" v-for="(item,i) in listdata" :key="i" style="margin-bottom: 20px;">
				<view class="card-top">
					<view class="card-toptitle01">
						<view class="card-content">
							<view class="card-maintitle01">
								座位名称：
							</view>
							<view class="card-maintitle02">
								{{item.seatdesc}}
							</view>
						</view>
					</view>
					<view class="card-toptitle02">
						<text style="color: cornflowerblue;font-weight: bold;"
							@click="updata(item.id,item.seatdesc,item.address,item.floor,item.status)">修改</text>
					</view>
				</view>
				<view class="card-top">
					<view class="card-toptitle01">
						<view class="card-content">
							<view class="card-maintitle01">
								座位地址：
							</view>
							<view class="card-maintitle02">
								{{item.address}}
							</view>
						</view>
					</view>
					<view class="card-toptitle02">
						<text style="color: coral;font-weight: bold;" @click="deletefun(item.id)">删除</text>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import homecom from "@/components/homecom/homecom.vue"
	export default {
		mounted() {
			this.init()
		},
		components: {
			homecom
		},
		data() {
			return {
				listdata: [],
				tabArr: [],
				TabCur: 0,

			}
		},
		methods: {
			async init() {
				this.queryfloor()
				this.queryseat(0)


			},
			async queryfloor() {
				let queryobj = {}
				let resdata = await new this.Request(this.Urls.m().floorquery, queryobj).modepost()
				console.log(resdata)
				resdata.forEach((val, i) => {
					this.tabArr.push(val.floordesc)
				})
			},
			async queryseat(index) {
				let queryobj = {
					floor: index + ''
				}
				let resdata = await new this.Request(this.Urls.m().byfloorquery, queryobj).modepost()
				this.listdata = resdata

			},
			gosearch(val) {
				uni.navigateTo({
					url: `../search/search?type=${val}`
				});
			},
			updata(id, seatdesc, address, floor,status) {
				uni.navigateTo({
					url: `../setseatdetail/setseatdetail?id=${id}&seatdesc=${seatdesc}&address=${address}&floor=${floor}&status=${status}`
				});
			},
			adduser() {
				uni.navigateTo({
					url: `../setseatdetail/setseatdetail?id=add`
				});
			},
			tabSelect(e) {
				this.TabCur = e.currentTarget.dataset.id;
				this.queryseat(this.TabCur)
				this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
			},
			deletefun(val) {
				uni.showModal({
					content: '确定要删除吗？',
					cancelText: '取消',
					confirmText: '确定',
					success: res => {
						if (res.confirm) {
							this.asdeletefun(val)
						}
					}
				})
			},
			async asdeletefun(val) {
				let obj = {
					id: val
				}
				let resdata = await new this.Request(this.Urls.m().seatdelete, obj).modepost()
				if (resdata == 'success') {
					this.alertobj.diagfun('操作成功')
					setTimeout(() => {
						this.queryseat(0)
					}, 1500)
				}
			}

		}
	}
</script>

<style scoped>
	.mainsub {
		padding-left: 5%;
		padding-right: 5%;
	}

	.card-main {
		width: 100%;
		height: 100px;
		background-color: #f1f1f1;
		border-radius: 5px;
		margin-top: 10px;
		padding: 5%;
		position: relative;
		border: 1px #f1f1f1 solid;
		box-shadow: darkgrey 2px 2px 2px 2px;
	}

	.card-top {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.card-content {
		display: flex;
		justify-content: flex-start;
		align-items: center;
	}


	.card-maintitle01 {
		font-weight: bold;
	}

	.card-maintitle01,
	.card-maintitle02 {
		margin-top: 10px;
	}

	.card-toptitle02 {
		margin-top: 10px;
	}

	.rightdetail {
		display: flex;
		justify-content: center;
		align-items: center;
	}

	.text-xsl {
		margin-top: 20%;
		text-align: center;
		font-size: 120px;
	}

	.textclass {
		margin-top: 5px;
		font-weight: 600;
		font-size: 15px;
		color: darkgray;
	}
</style>
